import chooseImg from './editGood-ChooseImg'
export default {
    components: {
        chooseImg
    },
    props: {
        label: {
            type: String,
            default: '标签'
        },
        tips: {
            type: String,
            default: ''
        },
        maxNum: {
            type: Number,
            default: 1
        },
        button: {
            type: String,
            default: '照片'
        }
    },
    data() {
        return {
            dataName: 'lksdjfk'
        }
    },
    methods: {
        selectCell() {
            this.$emit('click', {
                label: this.label,
                value: this.value
            })
        },
        getImages(list) {
            this.$emit('getImages', {
                images:list,
                label:this.label,
                maxNum:this.maxNum,
                button:this.button,
                tips:this.tips
            })
        },
        startmove(bool){
             this.$emit('startmove',bool)
        }
       
    },
}
